.section__Employess {
	background: url('../img/Employyes/bg.webp') no-repeat center / cover;
	background-attachment: fixed;
	@include adaptiv-value('padding-top', 90, 20, 1);
	@include adaptiv-value('padding-bottom', 90, 40, 1);
}

.Employess {
}

.Employess__inner {
}
.Employyes__header {
	color: $sec-text-col;
	text-align: center;
	@include adaptiv-value('margin-bottom', 60, 25, 1);
	.Employyes__header-title {
		margin-bottom: 20px;
		font: $high-text-bold;
		@include adaptiv-value('font-size', 48, 26, 1);
	}
	.Employees__header-suptitle {
		font: $middle-text;
		@include adaptiv-value('font-size', 22, 17, 1);
	}
}

.Employyes__cards {
	display: flex;
	justify-content: center;
	@include respond-below(sm) {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 8px;
	}
	@include respond-below(sx) {
		grid-template-columns: 1fr;
		justify-items: center;
	}
	.Employyes__card {
		margin-right: 10px;

		@include respond-above(sm) {
			cursor: pointer;
			&:hover {
				transition: 0.6s ease-out;
				transform: scale(1.05);
			}
		}

		@include respond-below(sx) {
			margin-right: 0;
		}
		&:last-child {
			margin-right: 0;
		}
		.card__photo {
			@include adaptiv-value('margin-bottom', 30, 15, 1);
			img {
				height: 120px;
				width: 120px;
				border-radius: 50%;
			}
		}
		.Employyes__name {
			font: $middle-text;
			@include adaptiv-value('font-size', 22, 16, 1);
		}
		.Employyes__profession {
			font: $small-text;
			margin-bottom: 25px;
		}
		.Employyes__text {
			@include adaptiv-value('font-size', 16, 13, 1);
		}
		.card__inner {
			background: #edf0f1;
			max-width: 306px;
			padding: 20px;
			display: grid;
			justify-items: center;
			text-align: center;
		}
	}
}
