.section__Reviews {
	background: url('img/Reviews/bg.jpg') repeat;
	@include adaptiv-value('padding-top', 80, 40, 1);
	@include adaptiv-value('padding-bottom', 80, 40, 1);
}

.Reviews {
}
.Reviews__header {
	text-align: center;
	margin-bottom: 20px;
	.Reviews__header-title {
		font: $high-text-bold;
		@include adaptiv-value('font-size', 39, 26, 1);
	}
	.Reviews__header-suptitle {
		font: $small-text;
		@include adaptiv-value('margin-top', 30, 15, 1);
	}
}

.Reviews__persons {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	justify-items: center;
	@include respond-below(sx) {
		grid-template-columns: repeat(1, 1fr);
	}

	.Review__person {
		display: flex;
		margin-bottom: 40px;
		@include respond-below(md) {
			display: grid;
			justify-items: center;
		}
	}
}
.Review__person-photo {
	margin-right: 20px;
	img {
		border-radius: 50%;
		width: 150px;
		height: 150px;
	}
}

.Review__person-info {
	max-width: 280px;
	@include respond-below(md) {
		text-align: center;
	}
	.Review__info-name {
		margin-bottom: 15px;
		@include adaptiv-value('font-size', 18, 20, 2);
		@include respond-below(xs) {
			font-size: 20px;
		}
	}
	.Review__info-text {
	}
}
