.section__About {
	@include adaptiv-value('padding-top', 100, 30, 1);
	@include adaptiv-value('padding-bottom', 70, 30, 1);
}

.About__card {
	@include adaptiv-value('padding-bottom', 100, 30, 1);
}
.About__card-inner {
	position: relative;
	display: flex;
	justify-content: center;
	@include respond-below(sm) {
		display: grid;
	}
	.card__text {
		background: $square;
		max-width: 400px;
		position: relative;
		padding: 60px 50px;
		@include adaptiv-value('padding-top', 60, 14, 1);
		@include adaptiv-value('padding-bottom', 60, 14, 1);
		@include adaptiv-value('padding-left', 50, 10, 1);
		@include adaptiv-value('padding-right', 50, 10, 1);

		z-index: 3;
		.card__text-title {
			font: $very-small-text-bold;
			text-transform: uppercase;
			margin-bottom: 45px;
			@include adaptiv-value('font-size', 32, 20, 1);
			&:after {
				content: '';
				position: absolute;
				width: 50px;
				height: 2px;
				border-bottom: 2px solid;
				color: $pri-text-col;
				padding-top: 20px;
			}
		}
		.card__text-suptitle {
			font: $small-text;
			margin-bottom: 30px;
		}
		.button {
			@include adaptiv-value('font-size', 16, 14, 1);
		}
	}

	.card__photo {
		img {
			position: relative;
			z-index: 2;
			width: 580px;
			height: 408px;
			top: -40px;
			left: -40px;
			max-width: 100%;
			@include respond-below(sm) {
				top: 0;
				left: 0;
			}
			@include respond-below(md) {
				width: 400px;
				height: 273px;
			}
		}
	}
}

.About__info {
	display: grid;
	justify-content: center;
}
.About__info-title {
	font: $middle-text-bold;
	text-transform: uppercase;
	max-width: 700px;
	@include adaptiv-value('font-size', 40, 26, 1);
	@include adaptiv-value('margin-bottom', 30, 15, 1);
}
.About__info-suptitle {
	font: $middle-text;
	color: $text-col-gray;
	@include adaptiv-value('font-size', 22, 17, 1);
	@include adaptiv-value('margin-bottom', 30, 15, 1);
}
.About__info-text {
	p {
	@include adaptiv-value('margin-bottom', 25, 15, 1);

	}
}
