.section__Feedback {
	background: url('../img/Feedback/bg.webp') repeat;
	@include adaptiv-value('padding-top', 20, 10, 1);
	@include adaptiv-value('padding-bottom', 20, 10, 1);
}

.Feedback {
	display: flex;
	justify-content: center;
}
.Feedback__photo {
	@include respond-below(md) {
		display: none;
	}
}
.Feedback__form {
	@include adaptiv-value('padding', 60, 30, 1);
	@include respond-below(md) {
		max-width: 100%;
		width: 100%;
	}
}
.Feedback__form-header {
	.form__header-title {
		font: $middle-text-bold;
		text-transform: uppercase;
		@include adaptiv-value('margin-bottom', 20, 10, 1);
		@include adaptiv-value('font-size', 40, 24, 1);
	}
	.form__header-suptitle {
		font: $middle-text;
		@include adaptiv-value('margin-bottom', 20, 10, 1);
		@include adaptiv-value('font-size', 22, 16, 1);
	}
}

.form {
	.field {
		@include adaptiv-value('padding-top', 15, 10, 1);
		@include adaptiv-value('padding-bottom', 15, 10, 1);
	}
	input {
		display: inline-block;
		width: 100%;
		padding: 12px 10px 12px 0;
		border-bottom: 1px #000 solid;
		background: transparent;
	}
	input[value] {
		color: #bdb5b5;
	}
	.button {
		@include adaptiv-value('margin-top', 40, 20, 1);
		button {
			background-color: rgb(159, 136, 126);
			box-shadow: rgb(129, 106, 96) 0px 0.25em 0px 0.01em;
			color: $sec-text-col;
			border-color: transparent;
		}
	}
}
