.section__Advantage {
	background: url('../img/Advantage/bg.webp') no-repeat center / cover;
	background-attachment: fixed;
	@include adaptiv-value('padding-top', 100, 40, 1);
	@include adaptiv-value('padding-bottom', 140, 40, 1);
}

.Advantage {
	color: $sec-text-col;
	.section__title {
		text-align: center;
	}
	.suptitle {
		@include adaptiv-value('font-size', 40, 17, 1);
	}
}

.Advantage__inner {
	display: flex;
	justify-content: center;
	margin-top: 20px;
}
.Advantage__info {
	.Advantage__info-item {
		display: flex;
		position: relative;
		@include adaptiv-value('margin-bottom', 30, 20, 1);
	}
}

.Advantage__item-icon {
	position: absolute;
	left: 0;
	top: 0;
	color: #f9b642;
	@include adaptiv-value('font-size', 50, 36, 1);
}

.Advantage__item-text {
	max-width: 700px;
	@include adaptiv-value('padding-left', 80, 50, 1);
	.Advantage__text-title {
		font: $middle-text;
		margin-bottom: 15px;
		@include adaptiv-value('font-size', 22, 20, 1);
	}
	.Advantage__text-suptitle {
		font: $small-text;
		color: #faf9f9;
	}
}
