button {
	background-color:$header;
}
.header {
	position: sticky;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 50;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
		width: 100%;
		height: 100%;
		background:$header;
	}
}
.sticky {
	position: sticky;
}

.header__container {
	position: relative;
	height: 80px;
	@include flex-all-sb;
	.header__logo {
		position: relative;
		z-index: 3;
		.Logo {
			width: 200px;
			height: 44px;
		}
	}
	.header__menu {
		.header__list {
			position: relative;
			z-index: 2;
			@include flex-v-center;
			li {
				list-style: none;
				margin-right: 15px;
				&:last-child{
					margin-right: 0;
				}
			}
			.header__link {
				display: inline-block;
				margin: auto;
				text-decoration: none;
				font: $small-text;
				color: $sec-text-col;
				&:after {
					content: '';
					display: block;
					height: 3px;
					width: 0%;
					background-color: $sec-text-col;
					transition: width 0.3s ease-in-out;
				}
				&:hover:after,
				&:focus:after {
					width: 100%;
				}
			}
		}
	}
}

@include respond-below(smd) {
	body.lock {
		overflow: hidden;
	}
	.header__burger {
		position: relative;
		display: block;
		width: 50px;
		height: 25px;
		z-index: 3;
		&:before,
		&::after {
			content: '';
			position: absolute;
			background-color: $sec-text-col;
			border-radius: 4px;
			width: 100%;
			height: 4px;
			left: 0;
			transition: 0.3s ease 0s;
		}
		&:before {
			top: -2px;
			z-index: 3;
		}
		&::after {
			bottom: -2px;
		}
		span {
			position: absolute;
			background-color: $sec-text-col;
			border-radius: 4px;
			left: 0;
			width: 100%;
			height: 4px;
			top: 10px;
			transition: 0.3s ease 0s;
		}
		&.active:before {
			transform: rotate(45deg);
			top: 11px;
		}
		&.active:after {
			transform: rotate(-45deg);
			bottom: 11px;
		}
		&.active span {
			transform: scale(0);
		}
	}

	.header__container {
		.header__menu {
			position: fixed;
			overflow: auto;
			top: -100%;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: $header;
			padding:0  10px 20px 10px;
			transition: 0.4s ease 0s;
			&.active {
				top: 0;
			}
			.header__list {
				display: block;
				text-align: center;
				top: 25%;
				li {
					margin-bottom: 40px;
				}
				.header__link {
					@include adaptiv-value('font-size', 50, 30, 1);
					color: $sec-text-col;
					&::after{
						display: none;
					}
				}

			}
		}
	}
}
