.section__Service {
	background: #f5f5f5;
	@include adaptiv-value('padding-top', 70, 20, 1);
	@include adaptiv-value('padding-bottom', 70, 40, 1);
}
.Service {
}

.Service__cards {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 12px;

	@include adaptiv-value('margin-top', 40, 20, 1);
	@include respond-below(md) {
		grid-template-columns: repeat(3, 1fr);
	}
	@include respond-below(smd) {
		grid-template-columns: repeat(2, 1fr);
	}
	@include respond-below(sx) {
		grid-template-columns: repeat(1, 1fr);
	}
	.Service__card {
		.card__inner {
			display: grid;
			justify-content: center;
			justify-items: center;
			padding: 20px 35px;
			background: $sec-text-col;
			transition: 0.3s ease-out;
			&:hover {
				background: #d9d9d9;
			}
			.card__photo {
				cursor: pointer;
				img {
					width: 200px;
					height: 200px;
				}
				@include adaptiv-value('margin-bottom', 20, 10, 1);
			}
			.card__title {
				font: $pre-middle-text;
				color: #444444;
				text-align: center;
				@include adaptiv-value('font-size', 20, 16, 1);
				@include adaptiv-value('margin-bottom', 20, 10, 1);
			}
			.card__description {
				font: $small-text;
				color: #212121;
				max-width: 200px;
				text-align: center;
				@include adaptiv-value('font-size', 16, 13, 1);
				@include adaptiv-value('margin-bottom', 30, 15, 1);
			}
			.Price {
				font: $prehigh-text;
				color: #212121;
				@include adaptiv-value('margin-bottom', 30, 15, 1);
				@include adaptiv-value('font-size', 24, 20, 1);
			}
			.card__button {
				button {
					padding: 10px 60px;
					background: transparent;
					color: #9f887e;
					border: 1px solid #9f887e;
					box-shadow: 0 0.25em 0px 0.01em rgb(129, 106, 96);
					transition: 0.3s all;
					&:hover {
						box-shadow: none;
						transform: translateY(2px);
					}
				}
			}
		}
	}
}
