.section__Intro {
	background: url('../img/Intro/bg.webp') no-repeat center / cover;
	@include adaptiv-value('padding-top', 150, 20, 1);
	@include adaptiv-value('padding-bottom', 100, 40, 1);
}

.Intro__inner {
}

.Intro__text {
	color: $sec-text-col;
	@include adaptiv-value('margin-bottom', 75, 15, 1);
	.Intro__title {
		font: $very-hight-text-bold;
		margin-bottom: 10px;
		text-shadow: 2px 4px 3px $pri-text-col;
		@include adaptiv-value('font-size', 54, 26, 1);
	}
	.Intro__suptitle {
		font: $very-high-text;
		@include adaptiv-value('font-size', 28, 26, 1);
	}
	.Intro__offer {
		font: $prehigh-text;
		@include adaptiv-value('margin-top', 50, 15, 1);
		@include adaptiv-value('font-size', 24, 17, 1);
	}
}

.Intro__buttons {
	@include flex-h-center;
	@include respond-below(sm) {
		flex-wrap: wrap;
	}
}
.button {
		margin-top: 15px;
	&.brown {
		button {
			background: $button_brown;
			box-shadow: rgb(129, 106, 96) 0px 0.25em 1px 0.01em;
			border: $button_brown;
		}
	}
	&.transparent {
		button {
			background: transparent;
			box-shadow: rgb(225, 225, 225) 0px 0.27em 1px 0.01em;
			border: solid $sec-text-col 3px;
		}
	}
	button {
		width: 300px;
		color: $sec-text-col;
		padding: 15px 40px;
		max-width: 300px;
		border: solid 3px;
		text-transform: uppercase;
		transition: 0.3s ease-in-out;
		@include adaptiv-value('margin-right', 270, 50, 1);
		@include respond-below(xs) {
			max-width: 250px;
		}
		@include respond-below(md) {
			@include adaptiv-value('margin-right', 70, 20, 1);
		}
		&:hover {
			box-shadow: none;
			transform: translateY(2px);
		}
	}
}
