.section__Footer {
	padding: 40px 0;
	background: $footer;
}

.Footer {
	display: flex;
	justify-content: space-between;
	@include respond-below(sm) {
		display: inline-block;
	}
}

.Footer__logo {
	img {
		max-width: 200px;
		max-height: 90px;
	}
}
.Footer__logo-info {
	max-width: 380px;
	@include respond-below(sm) {
		margin-bottom: 20px;
	}
}
.Footer__columns {
	display: flex;
	@include respond-below(sm) {
		flex-wrap: wrap;
	}
	@include respond-below(xs) {
		grid-template-columns: 1fr;
	}
}
.column {
	margin-right: 60px;
	@include respond-below(sm) {
		margin-bottom: 20px;
	}
	&:last-child {
		margin-right: 0;
	}
	li {
		margin-bottom: 8px;
		a {
			color: #333;
			border-bottom: 1px transparent solid;
			&:hover {
				border-bottom: 1px $pri-text-col solid;
			}
		}

		&:last-child {
			margin-bottom: 0;
		}
		&:nth-child(n + 2):nth-child(-n + 5) {
			text-transform: uppercase;
			@include adaptiv-value('font-size', 16, 14, 1);
		}
	}
	.grey {
		color: #888;
		margin-bottom: 10px;
	}
}
