.section__Repair {
	background: url('img/Reviews/bg.jpg') repeat;
	@include adaptiv-value('padding-top', 50, 25, 1);
	@include adaptiv-value('padding-bottom', 50, 25, 1);
}

.Repair {
}
.Repair__inner {
	.Repair__inner-header {
		display: grid;
		justify-content: center;
		@include adaptiv-value('margin-bottom', 80, 30, 1);
		.section__title {
			@include adaptiv-value('margin-bottom', 50, 20, 1);
		}
	}
	.Repair__inner-info {
		color: #212121;
		margin: 0 auto;
		p {
			@include adaptiv-value('margin-bottom', 30, 15, 1);
		}
	}
}

video {
	width: 100%;
	height: auto;
	max-height: 100%;
}
